.magazine {
    margin: 0 auto;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    position: relative;
    width: 80% !important;
    margin-left: 20%;
    height: 75vh !important;
}
.page {
    margin: 10px 0px;
}
.magazine .page img {
    max-width: 100%;
    height: 95%;
}
